import { Component, ViewContainerRef } from '@angular/core';
import { NgIf } from '@angular/common';
import { Router, Event, NavigationStart } from '@angular/router';
import { ComponentsHelper } from 'ng2-bootstrap';

@Component({
    selector: 'vl-app',
    template: `
        <header *ngIf="showHeaderFooter()"></header>
        <div class="body"><router-outlet></router-outlet></div>
        <footer *ngIf="showHeaderFooter()"></footer>
    `
})

export class AppComponent {
     private viewContainerRef: ViewContainerRef;
    _currentInstruction: string = '';

    constructor(viewContainerRef: ViewContainerRef, private router: Router) {
        var self = this;
        self.viewContainerRef = viewContainerRef;
        self.router.events.subscribe(function(event: Event) {
            if(event instanceof NavigationStart) {
                self._currentInstruction = event.url;
            }
            // NavigationEnd
            // NavigationCancel
            // NavigationError
            // RoutesRecognized
        });

        // TODO - This should be fixed by angular2 developers
        // Will be removed later
        ComponentsHelper.prototype.getRootViewContainerRef = function() {
        // https://github.com/angular/angular/issues/9293
        if (this.root) {
          return this.root;
        }
        let comps = this.applicationRef.components;
        if (!comps.length) {
          throw new Error('ApplicationRef instance not found');
        }
          try {
              /* one more ugly hack, read issue above for details */
              let rootComponent = this.applicationRef._rootComponents[0];
              // this.root = rootComponent._hostElement.vcRef;
              this.root = rootComponent._component.viewContainerRef;
              return this.root;
          } catch (e) {
              throw new Error('ApplicationRef instance not found');
          }
        };
    }

    showHeaderFooter() {
        return this._currentInstruction.indexOf('signin') === -1;
    }
}
